import React, { useEffect, useRef } from "react";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import { searchChangeTargetRefAction } from "../../store/reducers/search-reducer";
import { TierBar } from "./tier-bar/TierBar";
import "./TierList.css"
interface TierListInterface {
   
}
export const TierList:React.FC<TierListInterface> = () => {
    const ratings = [
        { rating: "SSS", description: " /           ." },
        { rating: "SS", description: "  /,      ,     SSS." },
        { rating: "S", description: " /,      ,          S  SS ." },
        { rating: "A", description: " /,  -  ,    ." },
        { rating: "B", description: "  /,        ." },
        { rating: "C", description: " /,     ,         ." }
      ];
    const location = useLocation();
    const params = new URLSearchParams(location.search);
    const type = params.get('type')||"";
    const containerRef = useRef(null);
    const dispatch = useDispatch();
    useEffect(()=>{
        searchChangeTargetRefAction(dispatch,containerRef)
    },[])
    const tierListClass = () =>{
        switch (type){
            case "identities":
                return "tier-list--ego-ids";
            case "ego":
                return "tier-list--ego-ids";
            case "passives":
                return "tier-list--passives";            
        }
        return "";
    }
    const setupTierlist = () => {
        if (type === "passives") return ["battlePassives" , "supportPassives"];
        return [type];
    }
    const tierListName = (tierListParam:string|null) =>{
        switch (tierListParam){
            case "identities":
                return "";
            case "ego":
                return "";
            case "battlePassives":
                return " ";      
            case "supportPassives":
                return " ";        
        }
        return "";
    }
    return (
        <div ref={containerRef} className="tier-list-container">
            {setupTierlist().map((tierListParam)=>{
                return (
                    <div key={`${Math.random()}`} className={["tier-list" , tierListClass()].join(" ")}>
                        <span className="tier-list-name">{tierListName(tierListParam)}</span>
                            {ratings.map(({rating,description})=>{
                                return(
                                    <TierBar  rating={rating} tierListParam={tierListParam} description={description} key={`${rating}`}></TierBar>
                                )
                            })}
                    </div>
                )
            })}
        </div>
        
    )
}
